import React from "react";
import {
  CallbackInput,
  ContactsContent,
  ContactsForm,
  ContactsText,
  ContactsTitle,
  ContactsWrapper,
  InputTextInput,
  SectionContainer,
  DarkButton,
  FrontCarImg,
} from "../style";
import axios from "axios";
import { PaperPlaneTilt } from "@phosphor-icons/react";

const handleSubmit = async (event: {
  preventDefault: () => void;
  target: { name: { value: string }; phone: { value: number } };
}) => {
  event.preventDefault(); // Prevent default form submission

  const name = event.target.name.value; // Get the value of the name input
  const phone = event.target.phone.value; // Get the value of the phone input

  try {
    // Send a POST request to the Google Sheets API
    const response = await axios.post(
      "https://your-google-sheets-api-endpoint",
      {
        name: name,
        phone: phone,
      }
    );

    console.log("Form data submitted successfully", response);
  } catch (error) {
    console.error("Error submitting form data", error);
  }
};

const Contacts = () => {
  return (
    <SectionContainer>
      <div style={{ backgroundColor: "#fff", borderRadius: "20px" }}>
        <ContactsWrapper>
          <ContactsContent>
            <ContactsTitle>   </ContactsTitle>
            <ContactsText>
                  ,       
               c 
            </ContactsText>
          </ContactsContent>
          <ContactsForm>
            <CallbackInput>
              <InputTextInput type="text" name="name" placeholder=" " />
              <InputTextInput
                type="number"
                name="phone"
                aria-label=" "
                placeholder=" "
              />
              <DarkButton style={{ gap: "12px" }}>
                 <PaperPlaneTilt size={22} weight="bold" />
              </DarkButton>
            </CallbackInput>
          </ContactsForm>

          <FrontCarImg />
        </ContactsWrapper>
      </div>
    </SectionContainer>
  );
};

export default Contacts;
